*,
*:before,
*:after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    font-size: 62.5%;
}

body {
    background-color: #000;
    color: #fff;
    padding: 8rem;
}

.container {
    position: relative;
    overflow: hidden;
    border-radius: 5rem;
}

.slider {
    display: flex;
    width: 500%;
    height: 55rem;
    transition: all 0.25s ease-in;
    transform: translatex(0);
}

.box {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    overflow: hidden;
    position: relative;
}

.bg {
    padding: 2rem;
    background-color: rgba(0, 0, 0, .2);
    width: 55%;
    transform: skewX(7deg);
    position: absolute;
    height: 100%;
    left: -10%;
    padding-left: 20rem;
    transform-origin: 0 100%;
}

.bg:before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-color: inherit;
    pointer-events: none;
    transform: skewx(10deg);
}

.details {
    padding: 5rem;
    padding-left: 10rem;
    z-index: 100;
    grid-column: 1 / span 1;
    grid-row: -1;
}

h1 {
    font-size: 3.5rem;
    font-weight: 500;
    margin-bottom: 0.5rem;
}

p {
    display: inline-block;
    font-size: 1.3rem;
    color: #b5b4b4;
    margin-bottom: 2rem;
    margin-right: 5rem;
}

button {
    padding: 1rem 3rem;
    color: #fff;
    border-radius: 2rem;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
}

button:hover {
    opacity: 0.8;
}

button:focus {
    outline: none;
    border: none;
}

.slider .box1 {
    background-color: #500033;
}

.slider .box1 .inner {
    background-color: #f07;
}

.slider .box1 .inner:after,
.slider .box1 .inner:before {
    background-color: rgba(255, 0, 119, .4);
}

.slider .box1 button {
    background-color: #f07;
}


.slider .box2 {
    background-color: #000050;
}

.slider .box2 .inner {
    background-color: #03f;
}

.slider .box2 .inner:after,
.slider .box2 .inner:before {
    background-color: rgba(0, 51, 255, .4);
}

.slider .box2 button {
    background-color: #03f;
}

.slider .box3 {
    background-color: #00501d;
}

.slider .box3 .inner {
    background-color: #0f4;
}

.slider .box3 .inner:after,
.slider .box3 .inner:before {
    background-color: rgba(0, 255, 68, .4);
}

.slider .box3 button {
    background-color: #0f4;
}

.slider .box4 {
    background-color: #554d00;
}

.slider .box4 .inner {
    background-color: #ff4e00;
}

.slider .box4 .inner:after,
.slider .box4 .inner:before {
    background-color: rgba(255, 78, 0, .4);
}

.slider .box4 button {
    background-color: #ff4e00;
}

.slider .box5 {
    background-color: #300050;
}

.slider .box5 .inner {
    background-color: #8000ff;
}

.slider .box5 .inner:after,
.slider .box5 .inner:before {
    background-color: rgba(128, 0, 255, .4);
}

.slider .box5 button {
    background-color: #8000ff;
}

.illustration {
    grid-column: -2;
    grid-row: -1;
    justify-self: center;
}

.illustration div {
    height: 25rem;
    width: 18rem;
    border-radius: 3rem;
    background-color: #f07;
    position: relative;
    transform: skewX(-10deg);
}

.illustration div::after,
.illustration div::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 3rem;
    top: 0;
    left: 0;
}

.illustration div::after {
    transform: translate(4rem, -1rem);
}

.illustration div::before {
    transform: translate(2rem, -2rem);
}

.prev,
.next,
.trail {
    z-index: 999;
    position: absolute;
}

.prev,
.next {
    width: 4rem;
    cursor: pointer;
    opacity: 0.2;
    transition: all 0.3s ease;
}

.prev:hover,
.next:hover {
    opacity: 1;
}

.prev {
    top: 50%;
    left: 2%;
    transform: translatey(-50%);
}

.next {
    top: 50%;
    right: 2%;
    transform: translateY(-50%);
}

.trail {
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
    width: 60%;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1rem;
    text-align: center;
    font-style: 1.5rem;
}

.trail div {
    padding: 2rem;
    border-top: 3px solid #fff;
    cursor: pointer;
    opacity: 0.3s;
    transition: all 0.3s ease;
}

.trail div:hover {
    opacity: 0.6;
}

.active {
    opacity: 1 !important;
}